.academics {
  // border: 2px solid red;
  // height: 80vh;
  // background-color:  black;
  width: 100vw;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;

  table {
    padding: 10px;
    border: 2px solid white;
    margin: 50px;
    td {
      height: 30px;
      line-height: 20px;
    }
    td:first-child {
      width: 40%;
      // border: 2px solid white;
    }
  }
  table:hover{
      transform: scale(1.1);
  }

}
.about {
}

@media only screen and (max-width: 400px) {
  table {
    width: 300px;
    .table_disc {
      padding: 10px;
    }
  }
}
@media only screen and (min-width: 400px) and (max-width: 600px) {
  table {
    width: 400px;
    .table_disc {
      padding: 2px;
    }
  }
}
@media only screen and (min-width: 600px) {
    table {
        // transform: scale(1.1);
      width: 600px;
      .table_disc {
        padding: 2.5px;
      }
    }
  }