.academics {
  width: 100vw;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
}
.academics table {
  padding: 10px;
  border: 2px solid white;
  margin: 50px;
}
.academics table td {
  height: 30px;
  line-height: 20px;
}
.academics table td:first-child {
  width: 40%;
}
.academics table:hover {
  transform: scale(1.1);
}

@media only screen and (max-width: 400px) {
  table {
    width: 300px;
  }
  table .table_disc {
    padding: 10px;
  }
}
@media only screen and (min-width: 400px) and (max-width: 600px) {
  table {
    width: 400px;
  }
  table .table_disc {
    padding: 2px;
  }
}
@media only screen and (min-width: 600px) {
  table {
    width: 600px;
  }
  table .table_disc {
    padding: 2.5px;
  }
}/*# sourceMappingURL=Academics.css.map */