* {
  margin: 0px;
  padding: 0px;
  list-style: none;
  text-decoration: none;
  font-family: "Mukta", sans-serif;
  letter-spacing: 1px;
  transition: all 0.5s;
}

body{
  background-color: black;
}
.App {
    transition: all 0.5s;
  // background-image: url("./nikhil1.jpg") ;
  // background-repeat: no-repeat;
  // background-position: center;
  // background-size: cover;
  // opacity: 0.1;
  background-color: rgba($color: black, $alpha: 1);
  height: 100vh;
  width: 100vw;
  .home {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.img{
  position: absolute;
  top:0px;
  height: 100vh;
  width: 100%;
  z-index: -1;
  opacity: 0.5;
  margin: auto;
}
.Header1 {
  background-color: transparent;
    h1{
      font-size: 2.5em;
    }
    color: white;
    li{
      color: gray;
      font-size: 1em;
    }
    h3 {
      font-size: 1em;
    color: gray;
}
span {
    color: white;
    text-decoration: underline;
    text-decoration-color: aqua;
    text-decoration-thickness: 0.2em;
    text-underline-offset: 0.5px;
  }
  .header_links {
      color: gray;
      display: flex;
      margin-bottom: 3px;
      .listitem {
      margin: 5px 20px;
      .underline {
        height: 0.2em;
        width: 0px;
        margin: -1px;
      }
    }
    .listitem:first-child {
      margin: 5px 20px 3px 0px;
    }
    .active_link{
        color: white;
    }
    .listitem:hover {
      color: white;
      .underline {
        background: aqua;
        width: 100%;
      }
    }
  }
  .header_icons {
    color: red;
    display: flex;
    .iconitem {
      margin: 2px 10px;
      width: 2em;
      height: 2em;
      border-radius: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid white;
      background-color: gray;
      img{
          border-radius: 100%;
          margin: 3px;
          width: 2em;
          height: 2em;
        }
    }
    .firstchild{
        margin-left: 0px;
        background-color: white;
    }
    .iconitem:hover{
        background-color: white;
        border: 2px solid aqua;
    }
  }
}
.Header2 {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  color: white;
  align-items: center;
  justify-content: center;
  padding: 11px 0px;
  border-bottom: 0.5px solid white;
  border-top: 0.5px solid white;
  h3,
  h1 {
    display: none;
  }
  .header_links {
    display: flex;
    .listitem {
      margin-right: 5px;
      font-size: 1em;
      color: gray;
      .underline {
        background-color: aqua;
        width: 0px;
        height: 0.2em;
        margin: -1px;
      }
    }
    .listitem:first-child {
      margin-left: 2px;
    }
    .listitem:hover {
      color: white;
      .underline {
        height: 0.2em;
        width: 100%;
        margin: -1px;
      }
    }
    .active_link {
      color: white;
    }
  }
  .header_icons {
    display: flex;
    margin-left: auto;
    .iconitem {
      margin: 3px;
      width: 1.5em;
      height: 1.5em;
      border-radius: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid white;
      background-color: gray;
      img{
          border-radius: 100%;
          margin: 3px;
          width: 1.5em;
          height: 1.5em;
        }
    }
    .firstchild{
        background-color: white;
    }
    .iconitem:hover{
        border: 2px solid aqua;
        background-color: white;
    }
  }
}
